$(document).ready(() => {
	// Applying Active Class On Nav Link when CLicked
	$('.navbar .nav-link').on('click', function () {
		$('.navbar')
			.find('.active')
			.removeClass('active');
		$(this)
			.parent('.nav-item')
			.addClass('active');

		if (this.hash !== '') {
			event.preventDefault();
			var hash = this.hash;
			$('html, body').animate({
					scrollTop: $(hash).offset().top - 70,
				},
				1000
			);
		}
	});

	// Making img tag src image to parent's background image
	$('.hero-wrap').each(function () {
		$(this).css(
			'background-image',
			'url(' +
			$(this)
			.find('img')
			.attr('src') +
			')'
		);
		$(this)
			.find('img')
			.remove();
	});

	// Owl Carousel For Hero Slider

	var owlone = $('.owlOne');
	owlone.owlCarousel({
		loop: true,
		margin: 0,
		nav: false,
		dots: false,
		items: 1,
		autoplay: true,
		onInitialized: startProgressBar,
		onTranslate: resetProgressBar,
		onTranslated: startProgressBar
	});


	function startProgressBar() {
		// apply keyframe animation
		$(".slide-progress").css({
			width: "100%",
			transition: "width 5000ms"
		});
	}

	function resetProgressBar() {
		$(".slide-progress").css({
			width: 0,
			transition: "width 0s"
		});
	}

	// Go to the next item
	$('.right-slider-btn').click(function () {
		owlone.trigger('next.owl.carousel');
	});
	// Go to the previous item
	$('.left-slider-btn').click(function () {
		owlone.trigger('prev.owl.carousel');
	});

	// Text ReadMore (Extra Content Hidden)

	$(function () {
		// Readmore Text for ABOUT US Section
		var showChar = 605;

		$('.more').each(function () {
			var content = $(this).html();

			if (content.length > showChar) {
				var c = content.substr(0, showChar);
				var h = content.substr(showChar, content.length - showChar);

				var html =
					c +
					'<span class="moreellipses">' +
					'...' +
					'&nbsp;</span><span class="morecontent d-none"><span>' +
					h +
					'</span>&nbsp;&nbsp;</span>';

				$(this).html(html);
			}
		});

		$('.moreAbout').click(function () {
			$(this)
				.prev('.detail-text')
				.find('p span.moreellipses')
				.toggleClass('d-none');
			$(this)
				.prev('.detail-text')
				.find('p span.morecontent')
				.toggleClass('d-none');

			if ($(this).hasClass('toggled')) {
				$(this).removeClass('toggled');
				$(this).html('more about');
			} else {
				$(this).addClass('toggled');
				$(this).html('less about');
			}
		});
	});

	$(function () {
		// Readmore Text for SERVICE Section
		var showSChar = 105;

		$('.Smore').each(function () {
			var scontent = $(this).html();

			if (scontent.length > showSChar) {
				var c = scontent.substr(0, showSChar);
				var h = scontent.substr(showSChar, scontent.length - showSChar);

				var html =
					c +
					'<span class="moreellipses">' +
					'...' +
					'&nbsp;</span><span class="morecontent d-none"><span>' +
					h +
					'</span>&nbsp;&nbsp;</span>';

				$(this).html(html);
			}
		});

		$('.service-readmore').click(function () {
			$(this)
				.prev('.service-detail')
				.find('p span.moreellipses')
				.toggleClass('d-none');
			$(this)
				.prev('.service-detail')
				.find('p span.morecontent')
				.toggleClass('d-none');

			if ($(this).hasClass('toggled')) {
				$(this).removeClass('toggled');
				$(this).html('read more +');
			} else {
				$(this).addClass('toggled');
				$(this).html('read less -');
			}
		});
	});

	$('.counter-wrap').each(function () {
		var numb = $(this)
			.find('h2')
			.html();
		// console.log(numb);
		// var count = 0;

		for (let i = 0; i < numb; i++) {
			setInterval(function () {
				$(this)
					.find('h2')
					.html(i);
			}, 3000);
		}
	});

	// /*===================== Counter  Script ======================*/
	$(() => {
		var counters = [];
		counters = $('.counter-wrap').find('h2');
		var countersQuantity = counters.length;
		// console.log(counters + ',' + countersQuantity);
		var counterVal = [];

		for (i = 0; i < countersQuantity; i++) {
			counterVal[i] = parseInt(counters[i].innerHTML);
			// console.log(counter[i]);
		}
		var count = function (start, value, id) {
			var localStart = start;
			setInterval(function () {
				if (localStart < value) {
					localStart = localStart + 10;
					counters[id].innerHTML = localStart;
				} else {
					counters[id].innerHTML = value;
				}
			}, 0.5);
		};
		var cond = true;
		requestScrollAnimation(pos => {
			var elAlert = document.getElementById('counterNumbers');
			var isVisible = isScrolledIntoView(elAlert, true);

			if (isVisible) {
				if (cond) {
					for (var j = 0; j < countersQuantity; j++) {
						count(0, counterVal[j], j);
					}
					cond = false;
				}
			}
		});
	});
	// Owl Carousel for GALLERY

	var owlTwo = $('.owlTwo');
	owlTwo.owlCarousel({
		loop: true,
		margin: 0,
		nav: false,
		dots: false,
		responsive: {
			0: {
				items: 1,
			},
			600: {
				items: 2,
			},
			1000: {
				items: 4,
			},
		},
	});

	// Gallery SLdier Button
	$('.gallery-btn-right').click(function () {
		owlTwo.trigger('next.owl.carousel');
	});
	// Gallery SLdier Button previous item
	$('.gallery-btn-left').click(function () {
		owlTwo.trigger('prev.owl.carousel');
	});

	// Gallery Images
	// img-wrap class is used to copy image src to anchore tag
	// href value to activate lightbox
	$('.img-wrap').each(function () {
		$(this).attr(
			'href',
			$(this)
			.find('img')
			.attr('src')
		);
	});

	lightbox.option({
		showImageNumberLabel: false,
	});

	// Unlocking Video Under Thumbnail
	var monitor = setInterval(function () {
		var elem = document.activeElement;
		if (elem && elem.id == 'unlocked-video') {
			document.getElementById('vidimg').style.display = 'none';
			clearInterval(monitor);
		}
	}, 100);

	// Load more Trainers Section Content
	$('.blog').hide();
	$('.blog')
		.slice(0, 7)
		.show();
	$('.loadMore').on('click', function (e) {
		e.preventDefault();
		$('.blog:hidden')
			.slice(0, 3)
			.slideDown();
		if ($('.blog:hidden').length == 0) {
			$('.loadMore').text('No More Content');
		}
	});
});